<template>
    <div class="counters-section">
        <div class="counters-block">
            <div class="counter-block">
                <div class="counter-title">{{(depositedCount/1000000).toFixed(0)}} USDT</div>
                <div class="counter-text">{{$localizeFilter('landing_liquidity_providing_counters_section_text_1')}}</div>
            </div>

            <div class="counter-block">
                <div class="counter-title">{{participantsCount}}</div>
                <div class="counter-text">{{$localizeFilter('landing_liquidity_providing_counters_section_text_2')}}</div>
            </div>

            <div class="counter-block">
                <div class="counter-title">{{(totalEarnedCount/1000000).toFixed(6)}} USDT</div>
                <div class="counter-text">{{$localizeFilter('landing_liquidity_providing_counters_section_text_3')}}</div>
            </div>
        </div>
    </div>
</template>

<script>
import { defineComponent } from '@vue/composition-api'
import anime from 'animejs/lib/anime.es.js';

export default defineComponent({
    setup() {
        
    },
    data: ()=>({
        depositedCount: 0,
        participantsCount: 0,
        totalEarnedCount: 0
    }),
    methods: {
        setTotalEarnCount(val) {
            const obj = { n: this.totalEarnedCount }
            anime({
                targets: obj,
                n: val,
                round: 1,
                duration: 3000,
                easing: 'linear',
                update: () => {
                this.totalEarnedCount = obj.n
                }
            })
        },
        async loadData(animate) {
            const {deposited, participants, totalEarned} = await this.$store.dispatch('getLiquidityProvidingLandingCounters')
            this.depositedCount = deposited
            this.participantsCount = participants
            if (totalEarned !== this.totalEarnedCount && animate) {
                this.setTotalEarnCount(totalEarned)
            } else {
                this.totalEarnedCount = totalEarned
            }
        }
    },
    async mounted() {
        this.loadData(false)
        this.interval = setInterval(()=>{
            this.loadData(true)
        },3000)
    },
    unmounted() {
        clearInterval(this.interval)
    }
})
</script>

